"use client";

import React, { useState } from 'react';
import { useAppContext, ExpenseCategoryGroup } from '@/contexts/AppContext';
import { Button } from '@/components/ui/button';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter } from '@/components/ui/dialog';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { toast } from '@/hooks/use-toast';
import { PlusCircle } from 'lucide-react';

const AutoFillExpensesButton: React.FC = () => {
  const { addExpenseToList, fixedCategoryGroups, variableCategoryGroups } = useAppContext();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [expenseType, setExpenseType] = useState<'essential' | 'variable' | ''>('');
  const [category, setCategory] = useState<string>('');
  const [numberOfEntries, setNumberOfEntries] = useState<number>(5);
  const [availableCategories, setAvailableCategories] = useState<string[]>([]);

  const handleTypeChange = (type: 'essential' | 'variable' | '') => {
    setExpenseType(type);
    setCategory(''); // Reset category when type changes
    if (type === 'essential') {
      setAvailableCategories(fixedCategoryGroups.flatMap(group => group.options));
    } else if (type === 'variable') {
      setAvailableCategories(variableCategoryGroups.flatMap(group => group.options));
    } else {
      setAvailableCategories([]);
    }
  };

  const generateRandomAmount = (min: number, max: number) => {
    return Math.floor(Math.random() * (max - min + 1)) + min;
  };

  const handleSubmit = () => {
    if (!expenseType || !category || numberOfEntries <= 0) {
      toast({
        title: 'Error',
        description: 'Please select type, category, and enter a valid number of entries.',
        variant: 'destructive',
      });
      return;
    }

    for (let i = 0; i < numberOfEntries; i++) {
      const randomAmount = generateRandomAmount(50, 5000); // Random amount between 50 and 5000
      const expenseData = {
        name: `Auto Expense ${i + 1} - ${category}`,
        amount: randomAmount,
        date: new Date(),
        type: expenseType as 'essential' | 'variable', // Type assertion after validation
        category: category,
      };
      addExpenseToList(expenseData);
    }

    toast({
      title: 'Expenses Auto-Filled',
      description: `${numberOfEntries} expenses added to ${category}.`,
    });
    setIsModalOpen(false);
    // Reset form for next use
    setExpenseType('');
    setCategory('');
    setNumberOfEntries(5);
    setAvailableCategories([]);
  };

  return (
    <>
      <Button
        onClick={() => setIsModalOpen(true)}
        className="fixed bottom-4 right-4 z-50 p-4 h-auto bg-purple-600 hover:bg-purple-700 shadow-lg rounded-full"
        aria-label="Auto-fill expenses"
      >
        <PlusCircle className="h-6 w-6 mr-2" /> Auto-Fill Test Expenses
      </Button>

      <Dialog open={isModalOpen} onOpenChange={setIsModalOpen}>
        <DialogContent className="sm:max-w-md bg-card">
          <DialogHeader>
            <DialogTitle className="text-primary">Auto-Fill Expenses (Debug)</DialogTitle>
            <DialogDescription>
              Quickly add multiple mock expenses for testing purposes.
            </DialogDescription>
          </DialogHeader>
          <div className="space-y-4 py-3">
            <div>
              <Label htmlFor="expenseType">Expense Type</Label>
              <Select value={expenseType} onValueChange={(value) => handleTypeChange(value as 'essential' | 'variable' | '')}>
                <SelectTrigger id="expenseType">
                  <SelectValue placeholder="Select type" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="essential">Essential</SelectItem>
                  <SelectItem value="variable">Variable</SelectItem>
                </SelectContent>
              </Select>
            </div>
            
            {expenseType && (
              <div>
                <Label htmlFor="category">Sub-Category</Label>
                <Select value={category} onValueChange={setCategory} disabled={!expenseType}>
                  <SelectTrigger id="category">
                    <SelectValue placeholder="Select sub-category" />
                  </SelectTrigger>
                  <SelectContent>
                    {availableCategories.map(cat => (
                      <SelectItem key={cat} value={cat}>{cat}</SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            )}

            <div>
              <Label htmlFor="numberOfEntries">Number of Entries</Label>
              <Input 
                id="numberOfEntries" 
                type="number" 
                value={numberOfEntries} 
                onChange={(e) => setNumberOfEntries(parseInt(e.target.value, 10) || 1)} 
                min="1"
                placeholder="e.g., 5"
              />
            </div>
          </div>
          <DialogFooter>
            <Button variant="outline" onClick={() => setIsModalOpen(false)}>Cancel</Button>
            <Button onClick={handleSubmit} className="bg-purple-600 hover:bg-purple-700">Generate Expenses</Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </>
  );
};

export default AutoFillExpensesButton;
