"use client";

import React from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Expense } from '@/contexts/AppContext';
import { ScrollArea } from '@/components/ui/scroll-area';

interface RecentTransactionsListProps {
  expenses: Expense[];
}

const RecentTransactionsList: React.FC<RecentTransactionsListProps> = ({ expenses }) => {
  // Sort expenses by date in descending order and take the top 5 or fewer
  const recentTransactions = React.useMemo(() => {
    return [...expenses]
      .sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime())
      .slice(0, 5); // Show top 5 recent transactions
  }, [expenses]);

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('en-IN', { style: 'currency', currency: 'INR' }).format(amount);
  };

  const formatDate = (date: Date) => {
    return new Date(date).toLocaleDateString('en-IN', { day: '2-digit', month: 'short', year: 'numeric' });
  };

  return (
    <Card className="h-[400px] md:h-[500px] flex flex-col">
      <CardHeader>
        <CardTitle className="text-center text-lg sm:text-xl">Recent Transactions</CardTitle>
        <CardDescription className="text-center">Your latest 5 expense entries</CardDescription>
      </CardHeader>
      <CardContent className="flex-grow overflow-hidden">
        {recentTransactions.length === 0 ? (
          <div className="flex items-center justify-center h-full">
            <p className="text-muted-foreground">No transactions yet.</p>
          </div>
        ) : (
          <ScrollArea className="h-full pr-3">
            <div className="space-y-4">
              {recentTransactions.map((expense) => (
                <div key={expense.id} className="flex items-center justify-between p-3 bg-muted/50 rounded-lg shadow-sm">
                  <div className="flex flex-col">
                    <span className="font-medium text-sm text-foreground truncate max-w-[150px] sm:max-w-[200px]" title={expense.name}>{expense.name}</span>
                    <span className="text-xs text-muted-foreground">{expense.category}</span>
                  </div>
                  <div className="text-right">
                    <span className="font-semibold text-sm text-destructive">{formatCurrency(expense.amount)}</span>
                    <p className="text-xs text-muted-foreground">{formatDate(expense.date)}</p>
                  </div>
                </div>
              ))}
            </div>
          </ScrollArea>
        )}
      </CardContent>
    </Card>
  );
};

export default RecentTransactionsList;
