"use client";

import React from 'react';
import { PieChart, Pie, Cell, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { useAppContext, Expense } from '@/contexts/AppContext';
import { ChartContainer, ChartTooltipContent, ChartLegend, ChartLegendContent } from "@/components/ui/chart";
import type { ChartConfig } from "@/components/ui/chart";

interface ExpenseDonutChartProps {
  expenses: Expense[];
}

// Define a consistent color palette
const COLORS = [
  'hsl(var(--chart-1))',
  'hsl(var(--chart-2))',
  'hsl(var(--chart-3))',
  'hsl(var(--chart-4))',
  'hsl(var(--chart-5))',
  'hsl(var(--chart-6))', // Added one more for potential variety
  'hsl(var(--chart-7))', // Added one more for potential variety
];

const ExpenseDonutChart: React.FC<ExpenseDonutChartProps> = ({ expenses }) => {
  const { fixedCategoryGroups, variableCategoryGroups } = useAppContext();

  const data = React.useMemo(() => {
    const categoryTotals: { [key: string]: number } = {};
    expenses.forEach(expense => {
      categoryTotals[expense.category] = (categoryTotals[expense.category] || 0) + expense.amount;
    });

    return Object.entries(categoryTotals).map(([name, value]) => ({ name, value }));
  }, [expenses]);

  const chartConfig = React.useMemo(() => {
    const config: ChartConfig = {};
    data.forEach((item, index) => {
      config[item.name] = {
        label: item.name,
        color: COLORS[index % COLORS.length],
      };
    });
    return config;
  }, [data]);

  if (data.length === 0) {
    return (
      <Card className="h-[400px] md:h-[500px] flex flex-col">
        <CardHeader>
          <CardTitle>Expense Breakdown by Category</CardTitle>
        </CardHeader>
        <CardContent className="flex-grow flex items-center justify-center">
          <p className="text-muted-foreground">No expense data available for the chart.</p>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="h-[400px] md:h-[500px] flex flex-col">
      <CardHeader>
        <CardTitle className="text-center text-lg sm:text-xl">Expense Breakdown</CardTitle>
      </CardHeader>
      <CardContent className="flex-1 pb-0">
        <ChartContainer config={chartConfig} className="mx-auto aspect-square max-h-[400px]">
          <ResponsiveContainer width="100%" height="100%">
            <PieChart>
              <Tooltip
                cursor={false}
                content={<ChartTooltipContent hideLabel indicator="dot" nameKey="name" formatter={(value, name, props) => `${props.payload.name}: ${new Intl.NumberFormat('en-IN', { style: 'currency', currency: 'INR' }).format(value as number)}`} />}
              />
              <Pie
                data={data}
                dataKey="value"
                nameKey="name"
                cx="50%"
                cy="50%"
                innerRadius="50%" // Creates the donut shape
                outerRadius="80%"
                strokeWidth={2}
              >
                {data.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                ))}
              </Pie>
              <ChartLegend
                content={<ChartLegendContent nameKey="name" className="text-xs sm:text-sm" />}
                verticalAlign="bottom"
                align="center"
                wrapperStyle={{paddingTop: "10px"}}
              />
            </PieChart>
          </ResponsiveContainer>
        </ChartContainer>
      </CardContent>
    </Card>
  );
};

export default ExpenseDonutChart;
